@media screen and (min-width: 800px) {
  .navigation {
      display: flex;
      padding-right: 20px;
      justify-content: flex-end;
      width: 100vw;
      position: absolute;
      top: 0;
  }
}

.navigation h3 {
    padding: 0px 20px 0px 20px;
    font-size: 12px;
    font-family: 'Arial Narrow';
    letter-spacing: 5px;
    color: black;
    font-weight: 100;
}

 /* unvisited link */
.navigation h3 a:link {
    color: black;
    text-decoration: none;
  }
  
  /* visited link */
.navigation h3 a:visited {
    color: black;
    text-decoration: none;
  }
  
  /* mouse over link */
.navigation h3 a:hover {
    color: grey;
    text-decoration: none;
  }
  
  /* selected link */
.navigation h3 a:active {
    color: black;
  } 